body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  align-items: center;
  display: flex;
  justify-content: center;
  margin: 50px;
  margin: 50px 14% 50px 14%;
}

.logo {
  height: 50px; 
}

.music {
  position: absolute;
  background-color: transparent;
  border: 1px solid #feda4a;
  border-radius: 3px;
  color: #feda4a;
  top: 5px;
  left: 80%;
  width: 90px;
  height: 18px;
  float: right;
}
.button-container {
  align-items: center;
  background-color: #feda4a;
  border-radius: 25px;
  display: flex;
  height: 70px;
  justify-content: space-around;
  margin: 0 14% 0 14%;
  position: relative;
  z-index: 2;
}

.navbar {
  background: #feda4a; /* Old browsers */ /* FF3.6+ */ /* Chrome,Safari4+ */ /* Chrome10+,Safari5.1+ */ /* Opera 11.10+ */ /* IE10+ */
  background: linear-gradient(to bottom,  #feda4a 0%,#feda4a 7%,#0e0e0e 100%); /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#feda4a', endColorstr='#0e0e0e',GradientType=0 ); /* IE6-9 */ 
 }

.button {
  align-items: center;
  background-color: transparent;
  border-right: solid 1px #000000;
  display: flex;
  height: 100%;
  justify-content: center;
  padding-bottom: 5px;
  width: 100%;
}

.button.end {
  border-right: none;
  background-color: transparent;
} 

a {
  color: #000000;
  letter-spacing: 1px;
  text-decoration: none; 
  font-family: 'Pathway Gothic One', sans-serif;
  font-size: 1.6rem;
  font-weight: 600;
}

.favorites-btn {
  border-right: none;
}

a.active {
  color: #feda4a;
}

button:focus {
  outline: none;
}

.fav-cont {
  display: inline-block;
  background-color: black;
  border-radius: 5px;
  color: #feda4a;
  margin-left: 10px;
  padding: 0 6px 0 6px;
  text-align: center;
  width: 12px;
}


.loading {
  display: flex;
  justify-content: center;
}

.load {
  margin-top: 200px;
}
.card {
  width: 350px;
  height: 510px;
  border: 5px solid #feda4a;
  margin: 25px;
}

.image-container {
  display: flex;
  justify-content: flex-end;
}

.character-pic {
  height: 330px;
  width: 350px;
  z-index: 998;
  position: relative;
}

.favorite-btn {
  background-color: transparent;
  cursor: pointer;
  position: absolute;
  height: 50px;
  width: 50px;
  z-index: 999;
}

.star {
  margin: 10px 15px 0 0;
}

.card-info {
  align-items: center;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 0 20px 0 20px;
}

.character-name {
  font-family: 'Pathway Gothic One', sans-serif;
  font-size: 1.5rem;
  letter-spacing: 6px;
  text-align: center;
  margin: 10px 0 8px 0;
  padding: none;
}

ul {
  list-style: none;
  margin-top: 0;
  padding: 0;
}

li, h4 {
  color: #feda4a;
  font-size: .85rem;

}

span {
  font-family: 'Pathway Gothic One', sans-serif;
  font-size: 1.1rem;
  font-style: italic;
  letter-spacing: 1px;
  text-transform: uppercase;
}




.fade {
  position: relative;
  width: 100%;
  min-height: 50vh;
  top: -150px;
  background-image: linear-gradient(0deg, transparent, black 75%);
  z-index: 1;
}

.star-wars {
  display: flex;
  justify-content: center;
  position: relative;
  height: 300px;
  color: #feda4a;
  font-family: 'Pathway Gothic One', sans-serif;
  font-size: 600%;
  font-weight: 600;
  letter-spacing: 6px;
  line-height: 150%;
  -webkit-perspective: 350px;
          perspective: 350px;
  text-align: justify;
}

.crawl {
  position: relative;
  top: 99999px;
  -webkit-transform-origin: 10% 60%;
          transform-origin: 10% 60%;
  -webkit-animation: crawl 40s linear;
          animation: crawl 40s linear;
}

.crawl > .title {
  font-size: 125%;
  text-align: center;
}

.crawl > .title h1 {
  margin: 0 0 150px;
  line-height: 100%;
  text-transform: uppercase;
}

@-webkit-keyframes crawl {
    0% {
    top: -100px;
    -webkit-transform: rotateX(25deg)  translateZ(0);
            transform: rotateX(25deg)  translateZ(0);
  }
    100% { 
    top: -6000px;
    -webkit-transform: rotateX(25deg) translateZ(-2500px);
            transform: rotateX(25deg) translateZ(-2500px);
  }
}

@keyframes crawl {
    0% {
    top: -100px;
    -webkit-transform: rotateX(25deg)  translateZ(0);
            transform: rotateX(25deg)  translateZ(0);
  }
    100% { 
    top: -6000px;
    -webkit-transform: rotateX(25deg) translateZ(-2500px);
            transform: rotateX(25deg) translateZ(-2500px);
  }
}
.no-favs {
  color: #feda4a;
  letter-spacing: 2px;
  font-family: 'Pathway Gothic One', sans-serif;
  font-size: 3rem;
  margin-top: 100px;
}
.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 25px;
}


body {
  background-image: url(/swapibox/static/media/stars-in-space-moving-fast-background_v1njcikee__F0000.1ee9abc5.png);
}




